import React from 'react'
import {createRoot} from 'react-dom/client'
import {BrowserRouter} from 'react-router-dom'

import {registerServiceWorker} from '@bpinc/ad-service-worker-reg'
import {FullscreenWallboardApp} from '@bpinc/wallboard-ui'

import '@bpinc/lib-css-config'
import '@bpinc/lib-css-tailwind'


registerServiceWorker()

const root = createRoot(document.getElementById('root')!)

root.render(
    <BrowserRouter basename="/agent/wallboard">
        <FullscreenWallboardApp />
    </BrowserRouter>,
)
